.outerWrapper {
    background-color: beige;
    background: url(/xuyang-LSimpChinese/static/media/background.a30cc2fe.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.grid-container {
    display: grid;
    grid-template-rows: auto 500px auto;
    height: 100vh;
}

.bottom {
    text-align: center;
    margin: 0px auto;
    padding-top: 40px;
    position: relative;
}

.poolWrapper {
    margin-left: 10px;
    margin-right: 10px;
    height: 500px;
    border-style: solid;
    border-color: coral;
    border-radius: 45px;
}

.finalLine {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    background: rgba(236, 128, 89, 0.2);
    height: 135px;
    width: 100%;
    display: grid;
    grid-template-columns: 150px 150px;
}

.result {
    color: #f54480;
    font-size: 60px;
    box-shadow: 1px 9px 20px 6px coral;
    border-radius: 30px;
}

.grid-2-columns {
    display: grid;
    grid-template-columns: 50% 50%;
    width: 600px;
    height: 100%;
    margin: auto;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.grid-col {
    height: 100%;
    background-repeat: repeat-y;
    background-size: 120px;
    will-change: backgroundPosition;
    transition: 0.3s background-position ease-in-out;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
}

.cube {
    background: #ff7f50a1;
    height: 135px;
    width: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    position: absolute;
}

.btnGroup {
    display: inline !important;
    position: relative;
    width: 100%;
}

.styleBtn {
    width: 200px;
    height: 150px;
    border-radius: 30px;
    background: white;
    color: black;
    border-color: #007bff;
    font-size: 41px;
}

.styledRow {
    padding: 100px;
    text-align: center;
}

.centerDiv {
    width: 80%;
    margin: auto;
    text-align: center;
}
.modal-body {
    position: relative;
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    padding: 1rem;
    width: 500px;
    height: 300px;
}

.modal.show .modal-dialog {
    -webkit-transform: none;
    transform: none;
    top: 30%;
    -webkit-transform: translateY(-30%);
            transform: translateY(-30%);
}

.modal-content {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 600px;
    pointer-events: auto;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, .2);
    border-radius: .3rem;
    outline: 0;
    background: url(/xuyang-LSimpChinese/static/media/tian.c48cd068.jpg) no-repeat;
    margin: auto;
}

.infoWrapper {
    width: 300px;
    height: 300px;
    background: white;
    z-index: -1;
    position: absolute;
    top: 0;
    left: 300px;
    padding: 20px;
}

.hanziWrapper {
    width: 300px;
    height: 300px;
    background: white;
    z-index: 999;
    position: absolute;
    top: 0;
    left: 0px;
}

* {
    box-sizing: border-box;
}

body {
    font-family: sans-serif;
}

.scene {
    /* border: 1px solid #CCC; */
    margin: 40px 0;
    position: relative;
    width: 210px;
    height: 140px;
    margin: 40px auto;
    -webkit-perspective: 1000px;
    top: 130px;
}

.carousel {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-transform: translateZ(-288px);
            transform: translateZ(-288px);
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

.carousel__cell {
    position: absolute;
    width: 190px;
    height: 120px;
    left: 10px;
    top: 10px;
    border: 2px solid black;
    line-height: 116px;
    font-size: 80px;
    font-weight: bold;
    color: white;
    text-align: center;
}
