.outerWrapper {
    background-color: beige;
    background: url("./images/background.jpg") no-repeat;
    background-size: cover;
    background-position: center;
    height: 100vh;
    width: 100vw;
    overflow: hidden;
}

.grid-container {
    display: grid;
    grid-template-rows: auto 500px auto;
    height: 100vh;
}

.bottom {
    text-align: center;
    margin: 0px auto;
    padding-top: 40px;
    position: relative;
}

.poolWrapper {
    margin-left: 10px;
    margin-right: 10px;
    height: 500px;
    border-style: solid;
    border-color: coral;
    border-radius: 45px;
}

.finalLine {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    background: rgba(236, 128, 89, 0.2);
    height: 135px;
    width: 100%;
    display: grid;
    grid-template-columns: 150px 150px;
}

.result {
    color: #f54480;
    font-size: 60px;
    box-shadow: 1px 9px 20px 6px coral;
    border-radius: 30px;
}

.grid-2-columns {
    display: grid;
    grid-template-columns: 50% 50%;
    width: 600px;
    height: 100%;
    margin: auto;
    user-select: none;
}

.grid-col {
    height: 100%;
    background-repeat: repeat-y;
    background-size: 120px;
    will-change: backgroundPosition;
    transition: 0.3s background-position ease-in-out;
    transform: translateZ(0);
}

.cube {
    background: #ff7f50a1;
    height: 135px;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    position: absolute;
}

.btnGroup {
    display: inline !important;
    position: relative;
    width: 100%;
}

.styleBtn {
    width: 200px;
    height: 150px;
    border-radius: 30px;
    background: white;
    color: black;
    border-color: #007bff;
    font-size: 41px;
}

.styledRow {
    padding: 100px;
    text-align: center;
}

.centerDiv {
    width: 80%;
    margin: auto;
    text-align: center;
}